:root {
    /* COLORS */
    --white: #f3f3f3;
    --black: #333333;
    --light-grey: #888888;
    --grey: #666666; 
    --medium-grey: #696969;

    /* DIMENSIONS */
    --base-font-size: 10px;
    --mobile: 0;
    --mobile-landscape: 700px;
    --tablet: 1024px;
    --desktop: 1200px;
    
    /* MEDIA QUERIES */
    @custom-media --mobile-viewport (min-width: 0px);
    @custom-media --mobile-landscape-viewport (min-width: 700px);
    @custom-media --tablet-viewport (min-width: 1024px);
    @custom-media --desktop-viewport (min-width: 1200px);
}/* MOBILE FIRST */
html, body {
    font-size: 10px;
    margin: 0 auto;
    padding: 0;
    font-size: var(--base-font-size);
    max-width: var(--tablet);
    width:100%;
    height: 100%;
}

/* RESPONSIVE */
/* PRINT */

/* MOBILE FIRST */
@media (--mobile-landscape-viewport) {

    .row {
        width: 100%;
        display: table;
        table-layout: fixed;
    }

    .col {
        display: table-cell;
        vertical-align: top;
    }

    /*3x*/
    .col-3-3 {  width:100%; }
    .col-2-3 { width:66.666666%; }
    .col-1-3 { width:33.333333%; }

    /*4x*/
    .col-4-4 { width:100%; }
    .col-3-4 { width:75%; }
    .col-2-4 { width:50%; }
    .col-1-4 { width:25%; }

}

/* RESPONSIVE */
/* PRINT */button, 
.button {
    border:1px solid var(--white);
    background-color: var(--black);
    display: inline-block;
    color: var(--white);
    text-decoration: none;
    font-size: 1rem;
    text-transform: uppercase;
    padding:1rem 2rem;
    letter-spacing: 0.2rem;
    font-weight: border;
}

button:hover,
.button:hover {
    background-color: var(--white);
    border:1px solid var(--black);
    color: var(--black);
}body {
    font-size: 1.4rem;
    font-family: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
    background-color: var(--black);
    color:var(--white);
}

h1 {
    margin:2rem 0;
    font-family: "Abril Fatface";
    font-size: 3rem;
    line-height: 2.5rem;
    font-weight: 400;
    letter-spacing:0;
}

h2 {
    font-size: 2rem;
    color: var(--medium-grey);
    border-bottom:1px dashed;
    padding-bottom: 0.5rem;
    text-transform: uppercase;
}

h3 {
    font-size: 1.5rem;
}

p {
    font-size: 1.6rem;
    color: var(--light-grey);
}

a {
    color: var(--grey);
    cursor: pointer;
}

a:hover {
    color:var(--white);
}


@media (--tablet-viewport){
    h1 {
        font-size: 5rem;
        line-height: 4rem;
    }
    h2 {
        font-size:3rem;
    }
    h3 {
        font-size: 2rem;
    }
}.content {
    margin:0;
    padding:0 2rem;
}.splash {
    background-color: var(--black);
    width:100%;
    height: 100%;
    display: table;
}

.splash h1 {
    color: var(--white);
}

.splash .col {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}.content .row .col {
    padding:1rem;
}.menu ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: none;
    width: 100%;
}

@media (--tablet-viewport) {
    .menu ul {
        display: table;
        table-layout: fixed;
    }
}

.menu ul li {
    display: block;
}

@media (--tablet-viewport) {
    .menu ul li {
        display: table-cell;
    }
}

.menu ul li a {
    line-height: 4rem;
    font-size: 1.2rem;
    letter-spacing: .2rem;
    background-color: var(--black);
    text-decoration: none;
    display: block;
    color: var(--light-grey);
    text-align: center;
    text-transform: uppercase;
}

.menu ul li a:hover {
    background-color: var(--light-grey);
    color: var(--black);
}

.menu ul li+li {
    border-left: 0;
    border-top: 1px solid var(--grey);
}

@media (--tablet-viewport) {
    .menu ul li+li {
        border-left: 1px solid var(--grey);
    }
}

.menu.open {
    background-color: var(--black);
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 100;
}

@media (--tablet-viewport) {
    .menu.open {
        position: static;
        height: inherit;
    }
}

.menu.open ul {
    display: block;
}

@media (--tablet-viewport) {
    .menu.open ul {
        display: table;
    }
}.news {
    margin:3rem 0;
}.news {
    margin:3rem 0;
}.news {
    margin:3rem 0;
}.albums-wrapper {
     margin: 3rem 0;
}

.albums .album {
    display: inline-block;
    margin:1.5rem;
}

.albums .album .player {
    display: inline-block;
    width: 150px;
    height: 150px;
    background-size: contain;
    margin: 10px 0;
    margin: 1rem 0;
    font-size: 30px;
    font-size: 3rem;
    text-align: center;
    line-height: 150px;
    color: var(--grey);
}

.albums .album .player:before {
    content: "\25BA";
    background-color: var(--grey);
    opacity: .5;
    display: block;
    color: var(--light-grey);
}

.albums .album .player.active:before {
    content: "\2587";
    opacity: 1;
    background-color: transparent;
}

.albums .album .player:hover:before {
    color: #f3f3f3;
    opacity: 1;
    background-color: transparent;
}

.albums .album .name {
    font-size: 15px;
    font-size: 1.5rem;
}

.albums .album .artist {
    text-transform: uppercase;
}

.albums .album .genre {
    color: #666;
    line-height: 10px;
    line-height: 1rem;
}

@medium (--tablet-viewport) {
    .albums .album {
        float: left;
        width: 33.333333%;
    }
}.logo {
    padding:10px;
}.hamburger {
    display: inline-block;
    cursor: pointer;
    width: 50px;
    font-size: 40px;
    line-height: 50px;
    text-align: center;
}

.hamburger:hover {
    background-color: var(--white);
    color: var(--black);
}

@media (--tablet-viewport) {
    .hamburger {
        display: none;
    }
}